: .5rem;\n  display: flex;\n  justify-content: space-between;\n  align-items: center;\n  max-width: 350px;\n  max-height: 180px;\n  min-height: 180px;\n  transition: all 250ms ease-out; }\n.box:hover {\n  background: rgba(255, 255, 255, 0.7);\n  -webkit-transform: translateY(-20%);\n          transform: translateY(-20%); }\n.box a {\n  text-decoration: none;\n  color: black; }\n.icon {\n  font-size: 3rem;\n  padding: 1rem; }\n.text h3 {\n  text-transform: uppercase;\n  letter-spacing: 4px;\n  margin-bottom: 0; }\n.text p {\n  margin-top: 1rem;\n  line-height: 1.5rem;\n  text-align: left; }\n.text {\n  padding: .5rem; }\n/* MEDIAQUERIES */\n@media (max-width: 1160px) {\n  .icon {\n    font-size: 2rem;\n    padding: 0.5rem; }\n  .text h3 {\n    text-transform: uppercase;\n    letter-spacing: 4px;\n    margin-bottom: 0;\n    font-size: 1rem; }\n  .text p {\n    margin-top: 1rem;\n    line-height: 1.5rem;\n    text-align: left;\n    font-size: .8rem; }\n  nav li a:hover {\n    background: none;\n    color: black; } }\n@media (max-width: 850px) {\n  body {\n    height: 100%; }\n  .container-boxes {\n    flex-direction: column; }\n  .box:hover {\n    background: rgba(255, 255, 255, 0.7);\n    -webkit-transform: none;\n            transform: none; }\n  .titles h1 {\n    font-size: 3rem; } }\n\n/*# sourceMappingURL=data:application/json;base64, */"